import React, { useState } from 'react';
import { Link, withRouter } from 'react-router-dom';
import * as yup from 'yup';
import { FieldArray, FormikProvider, useFormik } from 'formik';
import { ThemeProvider } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import { FormControl, FormHelperText, FormLabel } from '@material-ui/core';
import NumberFormat from 'react-number-format';

import { AddPropertyToProduct } from '../../components/add-property-to-product';
import { Thumb } from '../../components/thumb';
import { PriceFormatInput } from '../../components/price-format-input';
import {
  getDateOfChange,
  postItemsToDatabase,
  putItemsToDatabase,
  storage,
} from '../../services';

import {
  theme,
  themeUploadBtn,
  useAddItemInputStyles,
  useAddItemLabelStyles,
  useAddItemTextareaStyles,
  useSaveButtonStyles,
  useUploadButtonStyles,
} from '../../styles/customizing-material-ui-components';

import './add-item.scss';

const AddItem = ({
  properties,
  productsError,
  itemId,
  editingProduct,
  clearSelectedProduct,
  createdProduct,
  editedProduct,
  productsSpinnerOpen,
  productsSpinnerClose,
}) => {
  const [image, setImage] = useState(null);

  const classesLabel = useAddItemLabelStyles();
  const classesInput = useAddItemInputStyles();
  const classesSaveBtn = useSaveButtonStyles();
  const classesUploadBtn = useUploadButtonStyles();
  const classesTextarea = useAddItemTextareaStyles();

  //         
  const priceFormat = (value) => {
    return value.toString().replace(/(\d)(?=(\d{3})+$)/g, '$1 ');
  };

  //   "" ,           ,
  //     (..     ,       ).
  const createUniqueProperties = (allProperties, propertiesOfProduct) => {
    //     ,      
    if(!propertiesOfProduct) {
      return allProperties;
    }
    //    ,        
    const uniqueResultTwo = propertiesOfProduct.filter(function (obj) {
      return !allProperties.some(function (obj2) {
        return obj.id === obj2.id;
      });
    });

    //    ,    ,     
    return allProperties.concat(uniqueResultTwo);
  };

  const validationSchema = yup.object().shape({
    itemName: yup
      .string()
      .typeError('  ')
      .trim(' ')
      .required(' '),
    price: yup
      .number()
      .typeError('  ')
      .integer('   ')
      .test('firstSymbol', '    ', (value) => {
        return value?.toString().charAt(0) !== '0'; //     0
      })
      .required(' '),
    file: itemId
      ? //   ID ,      
        yup.array().of(
          yup
            .object()
            .shape({
              file: yup
                .mixed()
                .test(
                  'fileSize',
                  '     150',
                  (value) => {
                    if (!value) return false;
                    return value.size < 153600;
                  }
                ),
              type: yup
                .string()
                .oneOf(
                  ['image/jpeg', 'image/png', 'image/pjpeg'],
                  '     .jpg,.jpeg,.png'
                ),
              name: yup.string(),
            })
            .nullable()
            .typeError(' ')
        )
      : yup
          .array()
          .of(
            yup
              .object()
              .shape({
                file: yup
                  .mixed()
                  .test(
                    'fileSize',
                    '     150',
                    (value) => {
                      if (!value) return false;
                      return value.size < 153600;
                    }
                  )
                  .required(),
                type: yup
                  .string()
                  .oneOf(
                    ['image/jpeg', 'image/png', 'image/pjpeg'],
                    '     .jpg,.jpeg,.png'
                  )
                  .required(),
                name: yup.string().required(),
              })
              .typeError(' ')
          )
          .required(),
    fileUrl: yup.string().nullable().typeError('  '),
    description: yup
      .string()
      .typeError('  ')
      .required(' '),
    propertiesOfProduct: yup.array().of(
      yup
        .object()
        .shape({
          id: yup
            .string()
            .typeError('  ')
            .required(' '),
          propertyName: yup
            .string()
            .typeError('  ')
            .required(' '),
          propertyType: yup
            .string()
            .typeError('  ')
            .required(' '),
          propertyValue: yup.lazy((value) => {
            switch (typeof value) {
              case 'number':
                return yup
                  .number()
                  .typeError('  ')
                  .required(' ');
              case 'string':
                return yup
                  .string()
                  .typeError('  ')
                  .required(' ');
              default:
                return yup
                  .array()
                  .of(
                    yup.object().shape({
                      propertyValue: yup
                        .string()
                        .typeError('  ')
                        .required(' '),
                    })
                  )
                  .required(' ');
            }
          }),
        })
        .required(' ')
    ),
  });

  const getFileSchema = (file) =>
    file && {
      file: file,
      type: file.type,
      name: file.name,
    };

  const fileHandleChange = (e) => {
    if (e.target.files[0]) {
      setImage(e.target.files[0]);
    } else setImage(null);
  };

  const getArrErrorsMessages = (errors) => {
    const result = [];
    errors &&
      Array.isArray(errors) &&
      errors.forEach((value) => {
        if (typeof value === 'string') {
          result.push(value);
        } else {
          Object.values(value).forEach((error) => {
            result.push(error);
          });
        }
      });
    return result;
  };

  const getError = (touched, error, index) => {
    return (
      touched && error && <FormHelperText key={index}>{error}</FormHelperText>
    );
  };

  const formik = useFormik({
    initialValues: {
      itemName: editingProduct.itemName,
      //   input        
      price: itemId ? editingProduct.price : '',
      file: undefined,
      fileUrl: editingProduct.fileUrl,
      dateOfChange: '',
      description: editingProduct.description,
      //    ,     ,     ,    
      propertiesOfProduct:
        itemId && editingProduct.propertiesOfProduct
          ? editingProduct.propertiesOfProduct
          : [],
    },
    validationSchema: validationSchema,
    onSubmit: async (values) => {
      productsSpinnerOpen();
      const { itemName, description, price, propertiesOfProduct } = values;
      const trimmedItemName = itemName.trim();
      const trimmedDescription = description.trim();
      const numberedPrice = parseInt(String(price).replace(/ /g, ''));
      const trimmedPropsOfProduct = propertiesOfProduct.map((props) => {
        if (typeof props.propertyValue === 'string') {
          return { ...props, propertyValue: props.propertyValue.trim() };
        } else if (Array.isArray(props.propertyValue)) {
          return {
            ...props,
            propertyValue: props.propertyValue.map((propValue) => {
              return {
                ...propValue,
                propertyValue: propValue.propertyValue.trim(),
              };
            }),
          };
        } else return props;
      });

      if (image) {
        //       ,        
        //    
        const fileNameWithRndNumber = `${image.name}_${Math.floor(
          Math.random() * 1000000
        )}`;
        const uploadTask = storage
          .ref(`images/${fileNameWithRndNumber}`)
          .put(image);
        await uploadTask.on(
          'state_changed',
          (snapshot) => {},
          (error) => {
            productsError(error);
          },
          () => {
            storage
              .ref('images')
              .child(fileNameWithRndNumber)
              .getDownloadURL()
              .then((url) => {
                const newValues = {
                  ...values,
                  itemName: trimmedItemName,
                  description: trimmedDescription,
                  price: numberedPrice,
                  propertiesOfProduct: trimmedPropsOfProduct,
                  file: [], //    , ..    
                  // realtime firebase,    firebase storage
                  fileUrl: url,
                };

                // ,   
                if (itemId) {
                  putItemsToDatabase(
                    { ...newValues, dateOfChange: getDateOfChange() },
                    itemId,
                    'products',
                    productsError,
                    productsSpinnerClose,
                    editedProduct
                  );
                } else {
                  postItemsToDatabase(
                    { ...newValues, dateOfChange: getDateOfChange() },
                    'products',
                    productsError,
                    productsSpinnerClose,
                    createdProduct
                  );
                }
              });
          }
        );
      }
      // ,   ,        (  ).
      else {
        const newValues = {
          ...values,
          itemName: trimmedItemName,
          description: trimmedDescription,
          price: numberedPrice,
          propertiesOfProduct: trimmedPropsOfProduct,
          file: [], //    , ..    
          // realtime firebase,    firebase storage
        };

        await putItemsToDatabase(
          { ...newValues, dateOfChange: getDateOfChange() },
          itemId,
          'products',
          productsError,
          productsSpinnerClose,
          editedProduct
        );
      }
    },
    validateOnBlur: true,
  });

  const {
    values,
    errors,
    touched,
    handleChange,
    handleBlur,
    isValid,
    handleSubmit,
    dirty,
    setFieldTouched,
    setFieldValue,
  } = formik;

  return (
    <FormikProvider value={formik}>
      {/*    arrayHelper   type file*/}
      <ThemeProvider theme={theme}>
        <div className={'add-item'}>
          <div className={'add-item-bordered-wrap'}>
            <form onSubmit={handleSubmit} className={'add-item-wrap'}>
              <div className={'buttons-wrap'}>
                <Link
                  to={'/'}
                  className={'button-back'}
                  onClick={clearSelectedProduct}
                >
                  
                </Link>
                <Button
                  disableRipple={true}
                  className={'button-save'}
                  classes={{
                    root: classesSaveBtn.root,
                    label: classesSaveBtn.label,
                  }}
                  type={'submit'}
                  disabled={!isValid || !dirty}
                  onClick={handleSubmit}
                >
                  
                </Button>
              </div>
              <div className={'add-item-head'}>
                <h5>
                  {itemId ? ' ' : ' '}
                </h5>
              </div>
              <div className={'add-item-body'}>
                <FormControl
                  error={Boolean(touched.itemName && errors.itemName)}
                >
                  <FormLabel
                    classes={{ root: classesLabel.root }}
                    className={'labels'}
                  >
                     <span className={'red-star'}>*</span>
                  </FormLabel>
                  <OutlinedInput
                    type="text"
                    variant="outlined"
                    notched={false}
                    placeholder=" "
                    multiline
                    classes={{
                      root: classesInput.root,
                      input: classesInput.input,
                    }}
                    name={'itemName'}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.itemName}
                  />
                  {getError(touched.itemName, errors.itemName)}
                </FormControl>

                <FormControl error={Boolean(touched.price && errors.price)}>
                  <FormLabel
                    classes={{ root: classesLabel.root }}
                    className={'labels'}
                  >
                     <span className={'red-star'}>*</span>
                  </FormLabel>
                  <NumberFormat
                    classesInput={classesInput}
                    onChange={handleChange} //     ,  NumberFormat  
                    onBlur={handleBlur} //     ,  NumberFormat  
                    values={values}
                    customInput={PriceFormatInput}
                    format={priceFormat}
                  />
                  {getError(touched.price, errors.price)}
                </FormControl>

                <FormControl error={Boolean(touched.file && errors.file)}>
                  <FormLabel
                    classes={{ root: classesLabel.root }}
                    className={'labels'}
                  >
                    <span className={'red-star'}>*</span>
                  </FormLabel>
                  <FieldArray name={'file'}>
                    {(arrayHelper) => (
                      <div>
                        <input
                          accept=".jpg,.jpeg,.png"
                          className={'upload-input'}
                          id="contained-button-file"
                          multiple
                          type="file"
                          name={'file'}
                          onBlur={handleBlur}
                          onChange={(event) => {
                            const { files } = event.target;
                            const file = getFileSchema(files.item(0));
                            setFieldTouched('file', true, false);
                            fileHandleChange(event);
                            values.fileUrl = null; //       
                            if (!file) {
                              arrayHelper.remove(0);
                              setFieldTouched('file', true, false);
                              values.fileUrl = editingProduct.fileUrl; //     (  ""),
                              //       
                            }
                            if (Array.isArray(values.file)) {
                              arrayHelper.replace(0, file);
                            } else {
                              arrayHelper.push(file);
                            }
                          }}
                        />
                        <label
                          className={'upload-bnt-label'}
                          htmlFor="contained-button-file"
                        >
                          <ThemeProvider theme={themeUploadBtn}>
                            <Button
                              variant="contained"
                              component="span"
                              disableRipple={true}
                              classes={{
                                root: classesUploadBtn.root,
                                label: classesUploadBtn.label,
                              }}
                              endIcon={
                                <i
                                  className="fa fa-upload"
                                  aria-hidden="true"
                                />
                              }
                            >
                              {values.file === undefined ||
                              values.file[0] === null ? (
                                <div className={'upload-btn-name'}>
                                   
                                </div>
                              ) : (
                                values.file[0].file.name
                              )}
                            </Button>
                          </ThemeProvider>
                        </label>
                      </div>
                    )}
                  </FieldArray>
                  {getArrErrorsMessages(errors.file).map((error, index) =>
                    getError(true, error, index)
                  )}
                </FormControl>
                {/*  ,     ,     
                                  Thumb*/}
                {values.fileUrl ? (
                  <img
                    src={values.fileUrl}
                    alt={' '}
                    className={'thumb svg-thumbnail mt-2'}
                  />
                ) : (
                  <Thumb
                    file={
                      values.file === undefined || values.file[0] === null
                        ? null
                        : values.file[0].file
                    }
                  />
                )}
                <FormControl
                  error={Boolean(touched.description && errors.description)}
                >
                  <FormLabel
                    classes={{ root: classesLabel.root }}
                    className={'labels'}
                  >
                    <span className={'red-star'}>*</span>
                  </FormLabel>
                  <OutlinedInput
                    type="text"
                    multiline={true}
                    rows={5}
                    inputProps={{ maxLength: 1000 }}
                    variant="outlined"
                    notched={false}
                    placeholder="     1000 "
                    className={'add-item-textarea'}
                    classes={{
                      root: classesTextarea.root,
                    }}
                    name={'description'}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.description}
                  />
                  {getError(touched.description, errors.description)}
                </FormControl>
              </div>
              <AddPropertyToProduct
                handleChange={handleChange}
                touched={touched}
                errors={errors}
                handleBlur={handleBlur}
                values={values}
                properties={itemId ? createUniqueProperties(properties, editingProduct.propertiesOfProduct) : properties}
                getError={getError}
                setFieldValue={setFieldValue}
                setFieldTouched={setFieldTouched}
              />
            </form>
          </div>
        </div>
      </ThemeProvider>
    </FormikProvider>
  );
};

export default withRouter(AddItem);
