import React from 'react';
import OutlinedInput from '@material-ui/core/OutlinedInput';

const PriceFormatInput = ({ classesInput, onChange, onBlur, values }) => {
  return (
    <OutlinedInput
    variant="outlined"
    notched={false}
    placeholder=" "
    className={'number-input'}
    classes={{
        root: classesInput.root,
        input: classesInput.input,
    }}
    name={'price'}
    onChange={onChange} //     ,  NumberFormat  
    onBlur={onBlur} //     ,  NumberFormat  
    value={values.price.toLocaleString('ru-RU')} // toLocaleString ,         
    />
  );
};

export { PriceFormatInput };
