import React, { useState } from 'react';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { Link, withRouter } from 'react-router-dom';
import { FormControl, FormHelperText, FormLabel } from '@material-ui/core';
import { ThemeProvider } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';

import { ErrorMessageText } from '../../components/error-message-text';
import { submitLogIn } from '../../services';

import {
  theme,
  useLoginButtonStyles,
} from '../../styles/customizing-material-ui-components';

import './login.scss';

const Login = ({ history }) => {
  const classes = useLoginButtonStyles();

  const [errorMessage, setErrorMessage] = useState(null);

  const validationSchema = yup.object().shape({
    email: yup
      .string()
      .typeError('  ')
      .min(4, '     4 ')
      .max(20, '     20 ')
      .required(' '),
    password: yup
      .string()
      .typeError('  ')
      .min(8, '     8 ')
      .required(' '),
  });

  const formik = useFormik({
    initialValues: {
      email: '',
      password: '',
      showPassword: false,
    },
    validationSchema: validationSchema,
    onSubmit: (values) => {
      submitLogIn(values)
        .then(() => {
          setErrorMessage(null);
          history.push('/');
        })
        .catch((e) => {
          console.log(e.message);
          setErrorMessage(e.message);
        });
    },
    validateOnBlur: true,
  });

  const {
    values,
    errors,
    touched,
    handleChange,
    handleBlur,
    isValid,
    handleSubmit,
    dirty,
    setFieldValue,
  } = formik;

  return (
    <div className={'login'}>
      <form onSubmit={handleSubmit} className={'form-wrap'}>
        <h4></h4>
        {errorMessage ? <ErrorMessageText message={errorMessage} /> : null}
        <div className={'login-wrap'}>
          <FormControl fullWidth error={Boolean(touched.email && errors.email)}>
            <FormLabel></FormLabel>
            <OutlinedInput
              type="text"
              variant="outlined"
              notched={false}
              placeholder=" "
              name={'email'}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.email}
            />
            {touched.email && errors.email && (
              <FormHelperText>{errors.email}</FormHelperText>
            )}
          </FormControl>
          <FormControl fullWidth error={Boolean(touched.password && errors.password)}>
            <FormLabel></FormLabel>
            <OutlinedInput
              notched={false}
              placeholder=" "
              type={values.showPassword ? 'text' : 'password'}
              name={'password'}
              onChange={handleChange}
              onBlur={handleBlur}
              value={values.password}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    name={'showPassword'}
                    onClick={() =>
                      setFieldValue('showPassword', !values.showPassword)
                    }
                    edge="end"
                  >
                    {values.showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              }
            />
            {touched.password && errors.password && (
              <FormHelperText>{errors.password}</FormHelperText>
            )}
          </FormControl>
        </div>
        <div className={'login-button'}>
          <ThemeProvider theme={theme}>
            <Button
              classes={{
                root: classes.root,
                label: classes.label,
              }}
              type={'submit'}
              disabled={!isValid || !dirty}
              onClick={handleSubmit}
            >
              
            </Button>
          </ThemeProvider>
        </div>
        <div className={'link'}>
          <Link to={'/registration'}></Link>
        </div>
      </form>
    </div>
  );
};

export default withRouter(Login);
